
.footer-nav {
  padding: 24px 0;
  color: #99a9bf;
  font-size: 14px
}
.footer-nav::after {
  content: '';
  display: block;
  clear: both
}
.footer-nav i {
  -webkit-transition: .3s;
  transition: .3s;
  color: #d9def1;
  vertical-align: baseline
}
.footer-nav-link {
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s
}
.footer-nav-link:hover {
  color: #20a0ff
}
.footer-nav-link:hover i {
  color: #20a0ff
}
.footer-nav-left {
  float: left;
  margin-left: -4px;
}
.footer-nav-right {
  float: right;
  margin-right: -4px;
}



