
.navbar {
  height: 35px;
  overflow: hidden;
}
.navbar:after, .navbar:before {
  content: " ";
  display: table;
}
.navbar li {
  position: relative;
  display: inline;
  float: left;
  white-space: nowrap;
  line-height: 35px;
}
.navbar li.active a, .navbar li.active a:focus, .navbar li.active a:hover {
  text-decoration: none;
}
.navbar li.disabled a, .navbar li.disabled a:focus, .navbar li.disabled a:hover {
  color: #999;
  text-decoration: none;
  background-color: transparent;
  cursor: not-allowed;
}
.navbar li a {
  display: block;
  padding-left: 11px;
  padding-right: 11px;
  font-size: 14px;
  text-align: center;
}
.navbar li a:focus, .navbar li a:hover {
  text-decoration: none;
}
.navbar li b {
  float: left;
  margin: 11px 0;
  width: 1px;
  height: 14px;
  background-color: #3A5E95;
  border-right: 1px solid #758FB5;
}
.navbar-title {
  background: #525252;
  border-right: 2px solid white;
  font-weight: 700;
}
.navbar-title a:hover {
  color: #ffffff !important;
}
.navbar-inline {
  display: inline-block;
}
.navbar-default {
  background-color: #305895;
}
.navbar-default li a {
  color: #ffffff;
}
.navbar-default li a:focus, .navbar-default li a:hover {
  color: #ff0000;
}
.navbar-border {
  border: 1px solid #3A5E95;
  border-top: 0;
}
.navbar-border li:first-child {
  border-right: 1px solid #3A5E95;
}
.navbar-border li:first-child a {
  color: #000000;
  font-weight: 900;
}
.navbar-brand li:first-child {
  border-right: 1px solid #fff;
}
.navbar-brand li:first-child a {
  font-weight: 900;
}
.navbar-links {
  height: 30px;
}
.navbar-links li {
  line-height: normal;
  padding: 5px 0;
}
.navbar-links li a {
  padding-left: 8px;
  padding-right: 9px;
  background: url(/framework/navbar/images/element.png) no-repeat -90px -188px;
  color: #000000;
  font-size: 12px;
  font-family: sans-serif, "Microsoft YaHei";
}
.navbar-links li a:hover {
  text-decoration: underline;
  color: #ff4900;
}
.navbar-links li.first {
  margin-left: -1px;
}
.navbar-links li .red a {
  color: #ff0000;
}



