
.side-nav {
  width: 100%;
  box-sizing: border-box;
  padding-right: 30px;
}
.side-nav li {
  list-style: none;
}
.side-nav ul {
  padding: 0;
  margin: 0;
  overflow: hidden;
}
.side-nav .nav-item a {
  font-size: 16px;
  color: #5e6d82;
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  text-decoration: none;
  display: block;
  position: relative;
  -webkit-transition: all .3s;
  transition: all .3s
}
.side-nav .nav-item a.active {
  color: #20a0ff;
}
.side-nav .nav-item .nav-item a {
  display: block;
  height: 40px;
  line-height: 40px;
  font-size: 13px;
  padding-left: 24px
}
.side-nav .nav-item .nav-item a:hover {
  color: #20a0ff;
}
.side-nav .nav-group__title {
  font-size: 12px;
  color: #99a9bf;
  padding-left: 8px;
  line-height: 26px;
  margin-top: 10px;
}



