/*

Colorbrewer theme
Original: https://github.com/mbostock/colorbrewer-theme (c) Mike Bostock <mike@ocks.org>
Ported by Fabrício Tavares de Oliveira

*/
.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #fff;
}
.hljs, .hljs-subst {
  color: #000;
}
.hljs-string, .hljs-meta, .hljs-symbol, .hljs-template-tag, .hljs-template-variable, .hljs-addition {
  color: #756bb1;
}
.hljs-comment, .hljs-quote {
  color: #636363;
}
.hljs-number, .hljs-regexp, .hljs-literal, .hljs-bullet, .hljs-link {
  color: #31a354;
}
.hljs-deletion, .hljs-variable {
  color: #88f;
}
.hljs-keyword, .hljs-selector-tag, .hljs-title, .hljs-section, .hljs-built_in, .hljs-doctag, .hljs-type, .hljs-tag, .hljs-name, .hljs-selector-id, .hljs-selector-class, .hljs-strong {
  color: #3182bd;
}
.hljs-emphasis {
  font-style: italic;
}
.hljs-attribute {
  color: #e6550d;
}
@font-face {
  font-family: 'icomoon';
  src: url('assets/styles/fonts/icomoon.eot?h6xgdm');
  src: url('assets/styles/fonts/icomoon.eot?h6xgdm#iefix') format('embedded-opentype'),
    url('assets/styles/fonts/icomoon.ttf?h6xgdm') format('truetype'),
    url('assets/styles/fonts/icomoon.woff?h6xgdm') format('woff'),
    url('assets/styles/fonts/icomoon.svg?h6xgdm#icomoon') format('svg');
  font-weight: 400;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-rate-face-off:before {
  content: "\e900";
}
.icon-rate-face-1:before {
  content: "\e901";
}
.icon-rate-face-2:before {
  content: "\e902";
}
.icon-rate-face-3:before {
  content: "\e903";
}
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}
#app {
  height: 100%;
}
body {
  font-family: 'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei',SimSun,sans-serif;
  overflow: auto;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
}
a {
  color: #4078c0;
  text-decoration: none;
}
.hljs {
  line-height: 1.8;
  font-family: Menlo, Monaco, Consolas, Courier, monospace;
  font-size: 12px;
  padding: 18px 24px;
  background-color: #f9fafc;
  border: solid 1px #eaeefb;
  margin-bottom: 25px;
  border-radius: 2px;
  -webkit-font-smoothing: auto;
}
.main-cnt {
  margin-top: -80px;
  padding: 80px 0 120px;
  box-sizing: border-box;
  min-height: 100%;
  background-color:#f5f5f5;
}
.container, .page-container {
  width: 1140px;
  margin: 0 auto;
}
.page-container {
  padding-top: 55px;
}
.page-container h2 {
  font-size: 28px;
  color: #1f2d3d;
  margin: 0;
}
.page-container h3 {
  font-size: 22px;
}
.page-container h2, .page-container h3, .page-container h4, .page-container h5 {
  font-weight: 400;
  color: #1f2f3d;
}
.page-container p {
  font-size: 14px;
  color: #5e6d82;
}
.demo {
  margin: 20px 0;
}
@media (max-width: 1140px) {
.container, .page-container {
    width: 100%;
}
}



