
.page-changelog {
  padding-bottom: 100px;
}
.page-changelog .fr {
  float: right;
  padding: 0;
}
.page-changelog .fr a {
  display: block;
  padding: 10px 15px;
  color: #475669;
}
.page-changelog .fr:hover a {
  color: #20a0ff;
}
.page-changelog h2 {
  margin-bottom: 40px;
}
.page-changelog .timeline {
  margin: 0 0 0 105px;
  padding-left: 25px;
  position: relative;
  color: #5e6d82;
}
.page-changelog .timeline > li {
  list-style: none;
  position: relative;
  line-height: 1.8
}
.page-changelog .timeline > li:not(:last-child) {
  margin-bottom: 50px;
}
.page-changelog .timeline > li:first-child {
  margin-top: -10px;
}
.page-changelog .timeline > li:first-child h3:before {
  left: -33px;
  top: 10px;
  width: 17px;
  height: 17px;
  background-color: #20a0ff;
  border: 0;
}
.page-changelog .timeline ul {
  padding-left: 0;
}
.page-changelog .timeline li li {
  font-size: 14px;
  list-style: none;
  padding-left: 0
}
.page-changelog .timeline li li:before {
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #5e6d82;
  margin-right: 5px;
  display: inline-block;
  vertical-align: middle;
}
.page-changelog .timeline h3 {
  margin: 0 0 10px
}
.page-changelog .timeline h3:before {
  content: '';
  display: block;
  position: absolute;
  left: -31px;
  top: 13px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: transparent;
  border: 2px solid #20a0ff;
  box-sizing: border-box;
  background-color: #fff;
}
.page-changelog .timeline h4 {
  margin: 50px 0 10px;
}
.page-changelog .timeline p {
  margin: 0;
}
.page-changelog .timeline em {
  position: absolute;
  left: -127px;
  font-style: normal;
  top: 6px;
  font-size: 14px;
  color: #99a9bf;
}
.page-changelog .timeline:before {
  content: '';
  width: 1px;
  height: 100%;
  position: absolute;
  left: 0;
  top: 10px;
  background-color: #eaeefa;
}



