
.demo-color-box {
  border-radius: 4px;
  padding: 20px;
  height: 74px;
  box-sizing: border-box;
  color: #fff;
  font-size: 14px
}
.demo-color-box .value {
  font-size: 12px;
  opacity: 0.69;
  line-height: 24px
}
.demo-color-box-group .demo-color-box {
  border-radius: 0;
}
.demo-color-box-group .demo-color-box:first-child {
  border-radius: 4px 4px 0 0;
}
.demo-color-box-group .demo-color-box:last-child {
  border-radius: 0 0 4px 4px;
}
.bg-blue-light {
  background-color: rgba(48, 88, 149, 0.8);
}
.bg-blue, .bg-info {
  background-color: #305895;
}
.bg-blue-dark {
  background-color: #2a4d82;
}
.bg-success {
  background-color: #008000;
}
.bg-warning {
  background-color: #ff9212;
}
.bg-danger {
  background-color: #ff4802;
}
.bg-black {
  background-color: #1f2d3d;
}
.bg-black-light {
  background-color: #324057;
}
.bg-black-lighter {
  background-color: #475669;
}
.bg-silver {
  background-color: #8492a6;
}
.bg-silver-light {
  background-color: #99a9bf;
}
.bg-silver-lighter {
  background-color: #c0ccda;
}
.bg-gray {
  background-color: #d3dce6;
}
.bg-gray-light {
  background-color: #e5e9f2;
}
.bg-gray-lighter {
  background-color: #eff2f7;
}
.bg-white-dark {
