
.tag, .badge {
  display: inline-block;
  min-width: 10px;
  font-size: 12px;
  color: #000000;
  line-height: 1;
  vertical-align: middle;
  white-space: nowrap;
  text-align: center;
  padding: 3px 7px;
  border-radius: 4px;
}
.tag-default {
  color: #305895;
  border: 1px solid #305895;
}
.badge-default {
  color: #ffffff;
  background-color: #305895;
}
.tag-primary {
  color: #ff4802;
  border: 1px solid #ff4802;
}
.badge-primary {
  color: #ffffff;
  background-color: #ff4802;
}
.tag-success {
  color: #008000;
  border: 1px solid #008000;
}
.badge-success {
  color: #ffffff;
  background-color: #008000;
}
.tag-warning {
  color: #ff9212;
  border: 1px solid #ff9212;
}
.badge-warning {
  color: #ffffff;
  background-color: #ff9212;
}
.badge-sm {
  border-radius: 50%;
  line-height: 24px;
  width: 24px;
  height: 24px;
  font-size: 14px;
  margin: 0 5px;
  padding: 0;
}
.badge-md {
  border-radius: 50%;
  line-height: 28px;
  width: 28px;
  height: 28px;
  font-size: 18px;
  margin: 0 5px;
  padding: 0;
}
.badge-lg {
  border-radius: 50%;
  line-height: 32px;
  width: 32px;
  height: 32px;
  font-size: 22px;
  margin: 0 5px;
  padding: 0;
}




