
.table {
  border-collapse: collapse;
  width: 100%;
  max-width: 100%;
  margin-top: 10px;
  font-size: 12px;
  font-family: "Arial", "SimSun", sans-serif;
}
.table thead tr th, .table thead tr td, .table tbody tr td {
  padding: 7px;
}
.table thead tr th, .table thead tr td {
  text-align: center;
  border-bottom: 1px solid #bbd4e8;
}
.table tbody tr td {
  text-align: center;
}
.table-hover tbody tr:hover {
  background-color: #bbd4e8;
}
.table-border {
  border: 1px solid #bbd4e8;
}
.table-border thead th, .table-border thead td {
  color: #333;
  line-height: 1.4;
  padding: 5px;
}
.table-border th, .table-border td {
  border: 1px solid #bbd4e8;
}
.table-hover tbody tr td.default:hover, .table-hover tbody tr.default:hover td, .table-hover tbody tr:hover .default {
  background-color: #adc3e4;
}
.table tbody tr td.default, .table tbody tr.default td {
  background-color: #adc3e4;
}
.table-default {
  border: 1px solid #305895;
}
.table-default thead th, .table-default thead td {
  background-color: #adc3e4;
}
.table-default th, .table-default td {
  border: 1px solid #305895;
}
.table-hover tbody tr td.primary:hover, .table-hover tbody tr.primary:hover td, .table-hover tbody tr:hover .primary {
  background-color: #ffdcce;
}
.table tbody tr td.primary, .table tbody tr.primary td {
  background-color: #ffdcce;
}
.table-primary {
  border: 1px solid #ff4802;
}
.table-primary thead th, .table-primary thead td {
  background-color: #ffdcce;
}
.table-primary th, .table-primary td {
  border: 1px solid #ff4802;
}




