
.footer {
  height: 120px;
  background-color: #324057;
  color: #a4aebd;
  width: 100%;
  z-index: 1000;
  margin-top: -120px;
}
.footer * {
  word-spacing: 0;
}
.footer .container {
  height: 100%;
}
.footer .footer-main {
  font-size: 0;
  padding-top: 40px;
  display: inline-block;
}
.footer .footer-main .footer-main-title {
  line-height: 1;
  font-size: 22px;
  margin: 0;
}
.footer .footer-main .footer-main-link {
  display: inline-block;
  margin: 12px 18px 0 0;
  line-height: 1;
  font-size: 12px;
  color: #768193;
}
.footer .footer-main .footer-main-link a {
  color: #768193;
  text-decoration: none;
}
.footer .footer-social {
  float: right;
  line-height: 120px;
}
.footer .footer-social .footer-popover .el-popover {
  padding: 0;
  min-width: 120px;
  line-height: normal;
  box-shadow: 0 0 11px 0 rgba(174, 187, 211, 0.24);
}
.footer .footer-social .footer-popover .footer-popover-title {
  border-bottom: solid 1px #eaeefb;
  height: 30px;
  line-height: 30px;
  text-align: center;
  color: #99a9bf;
  background-color: #f8f9fe;
}
.footer .footer-social .footer-popover img {
  width: 100px;
  height: 100px;
  margin: 10px;
}
.footer .footer-social .elementdoc {
  -webkit-transition: .3s;
  transition: .3s;
  display: inline-block;
  line-height: 32px;
  text-align: center;
  color: #8D99AB;
  background-color: transparent;
  width: 32px;
  height: 32px;
  font-size: 32px;
  vertical-align: middle
}
.footer .footer-social .elementdoc:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.footer .footer-social .doc-icon-weixin {
  margin-right: 36px
}
.footer .footer-social .doc-icon-weixin:hover {
  color: #fff;
}
.footer .footer-social .doc-icon-github {
  margin-right: 0
}
.footer .footer-social .doc-icon-github:hover {
  color: #fff;
}



